@import url(https://fonts.googleapis.com/css2?family=Libre+Franklin:wght@900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #000;
  width: 100%;

}

.App {
  margin: 0 auto;
}

.Header {
    width: 100%;
    height: 300px;
    background: url(/gitfit/static/media/bkgImage.bfa41c67.jpg);
    background-size: cover;
    background-position: center;
}
.instagram {
    float: right;
    padding-right: 25px;
    padding-top: 75px;
    position: relative;
}


 
.Navigation {
    width: 100;
    min-height: 50px;
    text-align: center;
    background: #fff;
    font-size: 2em;
    font-weight: 100;
    padding-top: 7px; 
   
}
    .Navigation > a {
        text-decoration: none;
        margin: 0 13px;
        font-family: 'Libre Franklin', sans-serif;
        color: black;
    }

    .Navigation > a:hover {
        color:grey;
    }
.Home {
    width: 100%;
    text-align: center;
    font-size: 2em;
    color: #fff;
    padding-top: 115px;
    text-shadow: 3px 5px rgb(123, 33, 33);
    
    font-family: 'Libre Franklin', sans-serif;
}
.GritFitLogo{
    margin: 30px;
    margin-bottom: 115px;
    padding-top: 50px;
}

.About {
    padding-bottom: 200px;
   

}

.CharlesPic{
    margin-top: 50px;
    margin-left: 50px;
    margin-right: 30px;
    border: 10px solid #fff;
    float: left;
    position: relative;
}
.paragraph {
    text-align: left;
    color: #fff;
    font-size: 1.2em;
    font-family: 'Libre Franklin', sans-serif;
    padding-left: 100px;
    padding-top: 100px;
    margin-right: 50px;
}
.HeaderText {
    text-align: center;
    color: #fff;
    font-size: 2emem;
    font-family: 'Libre Franklin', sans-serif;
    padding-left: 100px;
    padding-top: 70px;
    text-shadow: 3px 5px rgb(123, 33, 33);
}

.Programs {
    text-align: center;
}
.programPic {
    margin-top: 30px;
    margin-bottom: 20px;
    border: 3px solid #fff;
    border-radius: 14px;

}
.Contact {
    text-align: center;
}
.Footer{
    width: 100%;
    height: 70px;
    background: #fff;
    margin-bottom: 45px;
    text-align: center;
    font-size: 1.3em;
    font-family: 'Libre Franklin', sans-serif;  
    padding-top: 15px;    
}
.Merch {
    text-align: center;

}
